.hidden {
  display: none;
}

.cat-info {
  display: flex;
  gap: 30px;
}
.cat-none {
  display: none;
}
h2 {
  color: brown;
}

.error {
  margin: 20px;
  text-align: center;
  color: red;
}

.error-none {
  display: none;
}

.loader {
  width: 70px;
  height: 70px;
  border: 5px solid tomato;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  position: fixed;
  left: 50%;
  bottom: 50%;
}
